<template>
  <div id="translateForm">
    <form v-on:submit="formSubmit">
      <div class="form-row mt-4">
        <div class="form-group col-md-12">
          <div class="input-group mb-3 mt-3">
            <div class="input-group-prepend">
              <span class="input-group-text" id="basic-addon1">Wpisz frazę</span>
            </div>
            <input type="text" class="form-control" v-model="textToTranslate" placeholder="Wpisz tutaj tekst" />
          </div>
        </div>
        <div class="form-group col-md-6">
          <div class="input-group mb-3">
            <div class="input-group-prepend">
              <label class="input-group-text" for="inputGroupSelect01">Wybierz język</label>
            </div>
            <select v-model="language" class="custom-select" id="inputGroupSelect01" required>
              <option v-for="lang in languages" v-bind:value='lang.code'>{{lang.label}}</option>
            </select>
          </div>
        </div>
        <div class="form-group col-md-6">
          <button type="submit" class="btn btn-primary btn-block">Tłumacz</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'translateForm',
  data(){
    return {
      textToTranslate : '',
      languages: {
        russian: { code: 'ru', label: 'Russian' },
        english: { code: 'en', label: 'English' },
        german: { code: 'de', label: 'German' }
      },
      language: ''
    }
  },
  methods: {
    formSubmit(e){
      this.$emit('formSubmit', this.textToTranslate, this.language);
      e.preventDefault();
    }
  }
}
</script>

<style>

</style>



