<template>
  <div id="app">
    <div class="container">
      <h1 class="text-center mt-5">Translate App</h1>
      <h6 class="text-center">Powered By Vue.js</h6>
      <TranslateForm
        v-on:formSubmit="translateText"></TranslateForm>
      <TranslateOutput v-text="translatedText"></TranslateOutput>
    </div>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm';
import TranslateOutput from './components/TranslateOutput';

export default {
  name: 'App',
  components: {
    TranslateForm,
    TranslateOutput
  },
  data: function(){
    return{
      translatedText: ''
    }
  },
  methods: {
    translateText:function(text, language){
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180916T120712Z.df76c9a7acb5ca01.44fa0daf002777eb9d2f099f7cbd633b966fed68&lang='+language+'&text='+text)
      .then((response) => {
        this.translatedText = response.body.text[0];
      });
    }
  }
}
</script>

<style>
#app {

}
</style>



